.charts-grid-item{max-height:100%;max-width:100%;display:inline-grid;text-decoration:none;grid-template-columns:1fr;grid-template-rows:minmax(230px,260px) 36px;color:#355c7d;-webkit-text-decoration-color:#355c7d;text-decoration-color:#355c7d}.charts-grid-item:hover{text-decoration:underline}.charts-grid-item--image{padding:15px;background-origin:content-box;border:1px solid #dddcde;border-radius:5px;background-size:contain;background-position:50%;background-repeat:no-repeat}.charts-grid-item--title{font-family:Poppins,sans-serif;text-transform:uppercase;font-weight:500;font-size:14px}@media screen and (min-width:768px){.charts-grid-item--title{font-size:16px}}